/*
// -------------------------------------------- //
//                 Admin Switch                 //
// -------------------------------------------- //
*/
.cartick-wrap {
	.cartick {
		&-content {
			&__switch {
				position: relative;
				display: inline-block;
				width: 40px;
				height: 24px;

				input {
					opacity: 0;
					width: 0;
					height: 0;
				}

				/* The slider */
				.slider {
					position: absolute;
					cursor: pointer;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					background-color: #ccc;
					transition: .4s;
					z-index: 2;
				}

				.slider:before {
					position: absolute;
					content: "";
					height: 18px;
					width: 18px;
					left: 3px;
					bottom: 3px;
					background-color: white;
					transition: .4s;
				}

				input:checked + .slider {
					background-color: $cartick-primary;
				}

				input:focus + .slider {
					box-shadow: 0 0 1px $cartick-primary;
				}

				input:checked + .slider:before {
					transform: translateX(16px);;
				}

				/* Rounded sliders */
				.slider.round {
					border-radius: 34px;
				}

				.slider.round:before {
					border-radius: 50%;
				}
			}
		}
	}
}
